<script>
//
import Contacto from "../components/Contacto.vue";
export default {
  name: "Home",
  data: () => ({
    //
    colores: [
      { claro: "#E91E63", oscuro: "#E91E63" },
      { claro: "#2196F3", oscuro: "#1E88E5" },
      { claro: "#F44336", oscuro: "#E53935" },
      { claro: "#4CAF50", oscuro: "#43A047" },
      { claro: "#009688", oscuro: "#009688" },
    ],
    color: "#E91E63",
    colorOscuro: "#E91E63",

    inicio: false,
  }),
  components: {
    //
    Contacto,
  },
  methods: {
    //
    seleccionarColor() {
      let aleatorio = Math.ceil(this.colorRandom()) - 1;
      this.color = this.colores[aleatorio].oscuro;
      this.colorOscuro = this.colores[aleatorio].claro;
      setTimeout(() => {
        this.seleccionarColor();
      }, 8000);
    },

    colorRandom() {
      return Math.random() * (this.colores.length - 0) + 0;
    },
  },
  mounted() {
    this.seleccionarColor();
    setTimeout(() => {
      this.inicio = true;
    }, 8000);
  },
};
</script>

